"use client";
import { useState } from "react";

export default function LoginPage() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [message, setMessage] = useState("");

  const handleLogin = async (e) => {
    e.preventDefault();
    try {
      const res = await fetch("http://localhost:5000/api/login", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ email, password }),
      });

      const data = await res.json();
      if (!res.ok) {
        setMessage(data.error || "Login failed");
        return;
      }

      localStorage.setItem("token", data.token);
      setMessage("Login successful! Redirecting...");
      setTimeout(() => (window.location.href = "/dashboard"), 1000);
    } catch (err) {
      setMessage("Backend error");
    }
  };

  return (
    <div className="min-h-screen bg-sky-200 flex items-center justify-center p-6">
      <form
        onSubmit={handleLogin}
        className="bg-white p-6 rounded shadow-md w-full max-w-sm flex flex-col gap-4"
      >
        <h1 className="text-2xl font-bold text-center text-blue-700">Login</h1>

        <input
          type="email"
          placeholder="Email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          className="border p-2 rounded focus:outline-blue-500"
          required
        />
        <input
          type="password"
          placeholder="Password"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          className="border p-2 rounded focus:outline-blue-500"
          required
        />

        <button className="bg-blue-600 text-white py-2 rounded hover:bg-blue-700">
          Login
        </button>
        {message && <p className="text-center text-red-500">{message}</p>}
      </form>
    </div>
  );
}
